.logo-image{
    display: flex;
    align-items: center;
    justify-content: center;
}

.nav-bar{
    display: flex;
    align-items: center;
    justify-content: center;
}



nav{
    padding-top: 2em;
    padding-bottom: 2em;
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 10vh;
    border-radius: 1em;
    color:white;
}

.nav-links{
    width:50h;
    border: 1px double #000000;
    display: flex;
    justify-content: center;
    align-items: center;
}

.nav-ind{
    margin: 10px 30px;
    font-size: 20px;
    font-family: 'Roboto', sans-serif;
    text-transform: uppercase;
    letter-spacing: 0.05em;
    color: #121212;
    list-style: none;
}

